@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
/* External Fonts */

/* Reset CSS */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #000;
}

body {
  font-family: 'Roboto Mono', monospace;
  background: #BFDBCF;
}

body, html {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

